body {
  margin: 0;
  padding: 0;
  height: 100vh;
  background: #efefef;
  color: rgb(2, 41, 66);
  font-family: "Raleway", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100vh;
}
.App {
  height: 100%;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 10% 30% auto;
}

.header{
  color:black;
  font-size: 2rem;
  font-weight: 500;
}
.Planes-root{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.Planes-title{
    font-weight: 200;
}
.Planes-noOfPlanes{
    font-weight: 600; 
    font-size: 3rem; 
    margin: 0.7rem 0;
}
.Planes-container{
    padding:0.5em;
    display:flex;
    border-radius: 25%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color:#d14143;
}
.PlanesInfo-root {
    display: grid;
    font-size: 0.7rem;
    padding:1em;
    grid-template-rows: 3rem auto;
}

.PlanesInfo-header {
    flex-grow: 1;
    display: grid;
    padding: 1em;
    color:#777;
    font-size: 0.6rem;
    grid-template-columns: 40% 20% 20% 20%;
}

.PlanesInfo-row {
    display: grid;
    padding: 0.5em;
    margin:0.5rem 0rem;
    grid-template-columns: 40% 20% 20% 20%;
    background: white;
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.05);
}
